@use "../util"as *;
@use "../globals/colours"as *;


//========= utility classes =========//

.nav-item {
    height: rem(50);
    padding: 0 rem(6) 0 rem(6);
}

.flex {
    display: flex;
    align-items: center;
}

.nav-text {

    color: var(--text-light);
    font-weight: 700;
    padding-left: 3px;
    font-size: rem(14);
    line-height: 1.2;
    flex-shrink: 0;
    white-space: nowrap;



    &::first-line {
        color: var(--text-less-light);
        font-size: rem(12);
        font-weight: 400;
    }
}

.nav-down-arrow {
    display: inline-block;
    width: 0;
    height: 0;
    border-width: 4px;
    border-style: solid;
    border-color: transparent;
    border-top-color: var(--down-arrow);
    border-bottom-width: 0;
    margin: 16px 0 0 2px;
}

.nav-icon {
    background: url(../assets/icons/icons.png);
}
.nav-focus{
    box-shadow: 0 0 0 3px var(--accent-active);

}
.nav-hover {
    &> :not(:nth-child(3)):hover {
        border-radius: 1px;
        box-shadow: 0 0 0 1px var(--text-light);
    }
}

.flag-icon {
    background: url(../assets/icons/flags.png);
}

//========= main component =========//

.nav {
    height: rem(60);
    min-width: min-content;
    width:100vw ;
    padding: rem(5) rem(15) rem(5) rem(13);
    background-color: var(--nav-dark);

    &__logo {


        &_img {
            display: inline-block;
            height: rem(30);
            width: rem(97);
            margin-top: 8px;
            background-position: -10px -51px;

        }


        &_text {
            color: var(--text-light);
            margin: -4px 4px 0 0;
        }

    }



    &__location {
        width: rem(165);

        &_logo {
            height: rem(18);
            width: 15px;
            margin-top: rem(10);
            background-position: -71px -378px;
            flex-shrink: 0;
        }

        &_text {
            width: 15ch;
        }
    }



    &__search {
        width: auto;
        flex-grow: 1;
        height: rem(40);       
        background-color: var(--light-bg);
        border-radius: rem(4);
        margin: 0 rem(15) 0 rem(15);


      
        &_categories {
            height: rem(40);
            width: max-content;
            padding: 0 rem(10);
            border-radius: rem(4) 0 0 rem(4);
            outline: none;
            border-right: 1px solid var(--text-less-light);
            background-color: var(--categories-label-bg);
            z-index: 100;
            position: relative;
            overflow: hidden;

            &:focus-within {
                box-shadow: 0 0 0 3px var(--accent-active);

            }

            &:hover {
                background-color: var(--categories-label-bg-active);

            }

            &_selected_label {
                font-size: rem(12);
                color: var(--categories-label);


            }

            &_selected_down_arrow {

                margin: 0 0 0 10px;
                border-top-color: #666;

            }

            &_list {
                height: 40px;
                opacity: 0;
                position: absolute;
                left: 0;
                width: auto;
                cursor: pointer;



            }
        }

        &_text {
            border: none;
            outline: none;
            font-size: 18px;
            flex-grow: 1;
            z-index: 99;
            padding: 7px;


        }

        &_button {
            background-color: var(--accent);
            background-position: 2px -280px;
            outline: none;
            border: none;
            width: 45px;
            height: 40px;
            z-index: 100;
            border-radius: 0 rem(4) rem(4) 0;

            cursor: pointer;

            &:focus {
                box-shadow: 0 0 0 3px var(--accent-active);

            }
        }
    }


    &__language {

        width: auto;
        padding: 10px;

        &_flag {
            display: inline-block;
            width: 24px;
            height: 18px;
            background-position: 0 -166px;
            margin-top: 10px
        }

        &_down_arrow {
            margin-left: 5px;
        }
    }


    &__signin {

        &_text {
            width: 13.7ch;
            &::first-line {
                color: var(--text-light);
              
            }
        }

        &_down_arrow {}
    }


    &__orders {


        &_text {
            width: 7.9ch;
            &::first-line {
                color: var(--text-light);
              
            }
        }
    }


    &__cart {

        &_icon {
            height: 26px;
            width: 38px;
            background-position: -10px -340px;
            position: relative;

        }

        &_count {
            position: absolute;
            color: var(--cart-count);
            font-weight: 700;
            font-size: rem(16);
            top: -5px;
            left: 17px;


        }

        &_text {
            color: var(--text-light);
            font-weight: 700;
            padding-left: 3px;
            font-size: rem(14);
            margin-top: 10px;

        }
    }

}